import { Component, OnInit, OnDestroy } from '@angular/core';
import { PostsService } from '../services/posts.service';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit{
listOfPosts:any;
  constructor(private service: PostsService) {     
}

  //Track by option - to save memory

 ngOnInit(){
  this.renderPosts();
 }

  renderPosts(){
    this.service.getPosts().subscribe(response => {
      this.listOfPosts = response;
      console.log(this.listOfPosts);
    })
  }




  addPost(inputElem :HTMLInputElement){
     //console.log("New Post :" + postTitle.value);
     let post = {title: inputElem.value};
     this.service.addPost(post).subscribe(response =>{
       this.listOfPosts.splice(0,0, post);
     })
     inputElem.value="";
  }

  update(post){
    this.service.update(post).subscribe(response =>{
      console.log(response);
    });
  }


  delete(post){
    this.service.delete(post).subscribe(Response =>{
      let index = this.listOfPosts.indexOf(post);
      this.listOfPosts.splice(index, 1);
    })
  }
 
  

}
