import { Component, Input, Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-about-child',
  template: `<p>Data received from Parent : {{ dataFromParent }} </p>
<button class="btn btn-primary" (click)="sendToParent()">send to parent</button>
            `,
  styleUrls: ['./about-child.component.css']
})
export class AboutChildComponent {
//Parent to Child - we use @Input()
  @Input('parentData') public dataFromParent ;

//Child to Parent - we use @Output()
  @Output() public childEvent = new EventEmitter();
  public childToParent = "I am coming from Child";

  sendToParent(){
    this.childEvent.emit(this.childToParent);
  }

  constructor() { }


}
